/*
 * This component manages two variables: ${str} and ${tld}.
 *
 * LOCAL STATE (user input)    temporary - when changes, do nothing
 * GLOBAL STATE                read only - if changes, do nothing
 * URL PARAMS                  when changes, sync to local/global
 * USER SUBMIT OR SELECT       sync local state to global/url
 */
import React, { createRef } from "react"
import { bindActionCreators } from "redux"
import { connect } from "react-redux"
import * as ui_actions from "src/redux/actions/ui"
import * as input_actions from "src/redux/actions/input"
import * as output_actions from "src/redux/actions/output"
import { Button, Input } from "antd"
import { Styled } from "./Search.styled"
import InputTld from "src/components/Search/InputTld"
// import WordPoss from "../WordPoss"
import PropTypes from "prop-types"
import { object_from_querystring } from "universal-common-scripts/functions/urls"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import { faSearch } from "@fortawesome/pro-solid-svg-icons"
import Header from "../Header"
// import BetaPreviewBanner from "./BetaPreviewBanner"
import { faAngleDown } from "@fortawesome/pro-solid-svg-icons"

/*
 * CAPTCHA:
 *
 * in production, wait for captcha success, or abort fetching data
 * in development, query data immediately
 */
// import { loadReCaptcha as loadRecaptchaV3, ReCaptcha as RecaptchaV3 } from "react-recaptcha-v3"
// import RecaptchaV2 from "react-google-invisible-recaptcha"
const PRODUCTION = process.env.GATSBY_ACTIVE_ENV !== "development"
// const CAPTCHA2_KEY = "6LeQt-MUAAAAAFHZGwmJnd58aStK2xF-6f8MBtm3"
// const CAPTCHA3_KEY = "6LfSN-MUAAAAAOxMUojSlBxkicjSeX1YLW8ds8C1"

/*
 * LIFECYCLE:
 *
 * constructor() --- page loaded, parse state from url or redux initial state
 * componentDidMount() --- :focus input field, fetch data
 * componentDidUpdate() --- respond to URL change (back button, navigate to home)
 * s ave_state()
 * --- validates 2 arguments (word, tld), sets default, parses tld from word,
 * --- then calls captcha_challenge()
 * captcha_challenge()
 * --- in production, waits for captcha success
 * --- then calls verifyReCaptchaV2()
 * verifyReCaptchaV2()
 * --- syncs local state to global/url
 */
class Search extends React.Component {
  constructor(props) {
    super(props)
    /*
     * default values
     */
    let url_obj = object_from_querystring(props.location.search)
    this.state = {
      str: (url_obj.str || props.input_str || "").trim(),
      tld:
        url_obj.tld || props.input_tld || Object.keys(props.tlds_user)[0] || Object.keys(props.tlds_checked)[0] || "",
      captcha2_token: "", // to compare old/new
      captcha3_token: "" // will be setState first, then used on click
    }
    /*
     * dom manipulation
     */
    this.Input_ref = createRef()
  }

  focusInput() {
    if (this.Input_ref.current && window.innerWidth > 700) {
      this.Input_ref.current.focus()
    }
  }
  blurInput() {
    if (this.Input_ref.current) {
      this.Input_ref.current.blur()
    }
  }

  componentDidMount() {
    /*
     * Auto fetch content on page load
     */
    this.captcha_challenge()
    /*
     * Must pre-load for RecaptchaV3
     */
    // loadRecaptchaV3(CAPTCHA3_KEY)
    /*
     * :focus this input field from outside this component
     */
    this.focusInput()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    /*
     * url state -> local state
     */
    if (this.props.location.search !== prevProps.location.search) {
      let url_obj = object_from_querystring(this.props.location.search)
      this.setState({ str: (url_obj.str || "").trim(), tld: url_obj.tld || "" }, this.persist_state)
    }
    /*
     * remember last captcha
     */
    if (this.props.input_captcha !== prevProps.input_captcha) {
      this.setState({ captcha: this.props.input_captcha })
    }
  }

  captcha_challenge = () => {
    this.blurInput()
    /*
     * if challenge is successful, captcha.execute() will call this.verifyReCaptchaV2()
     */
    // console.warn("Search.js captcha_challenge()")
    /*
     * no input, stop
     */
    // if (!this.state.str) {
    //   return
    // }
    /*
     * verify
     */
    if (PRODUCTION) {
      /*
       * auth is valid, not expired!
       * but maybe just initial high number,
       * to "fake it" the first time, to avoid challenge the first time...
       */
      // temporary - always just use V3, never V2
      if (true) {
        //this.props.auth_expires >= Date.now()
        // console.warn("Search.js captcha_challenge() if ( this.props.auth_expires >= Date.now() )")
        /*
         * auth current, not expired - use V3
         */
        this.verifyReCaptchaV3()
      } else if (this.recaptcha2 && this.recaptcha2.execute) {
        console.warn("Search.js captcha_challenge() else if ( this.recaptcha2 && this.recaptcha2.execute )")
        /*
         * auth is expired, or not exist - use V2
         */
        console.warn("this.recaptcha2.execute()")
        // this.recaptcha2.execute()
      }
    } else {
      /*
       * prevent duplicate captcha (if everything the same)
       * skip captcha in DEVELOPMENT environment
       */
      this.persist_state()
    }
  }

  verifyReCaptchaV3 = () => {
    /*
     * captcha response
     */
    /*
     * use
     */
    // this.props.input_actions.RX__set_inputs({ captcha3: captcha_response })
    if (typeof grecaptcha !== "undefined" && typeof grecaptcha.ready === "function") {
      grecaptcha.ready(() => {
        grecaptcha
          .execute("6LfSN-MUAAAAAOxMUojSlBxkicjSeX1YLW8ds8C1", { action: "fetchData" })
          .then((captcha_response) => {
            console.warn("EXECUTED CAPTCHA V3 TOKEN =", captcha_response)
            window.recaptcha3_token = captcha_response
            this.persist_state(captcha_response, 3)
          })
      })
    }
  }

  // verifyReCaptchaV2 = () => {
  //   /*
  //    * captcha response
  //    */
  //   let captcha_response = ""
  //   if (PRODUCTION) {
  //     captcha_response = this.recaptcha2.getResponse()
  //     this.recaptcha2.reset()
  //   }
  //
  //   /*
  //    * use
  //    */
  //   this.persist_state(captcha_response, 2)
  // }

  persist_state = (captcha_response, captcha_version) => {
    /*
     * save inputs (captcha, str, tld)
     */
    let inputs = {}
    if (captcha_version && captcha_response) {
      inputs["captcha" + captcha_version] = captcha_response
    }
    if (this.props.input_str !== this.state.str) {
      inputs.str = this.state.str.trim()
    }
    if (this.props.input_tld !== this.state.tld) {
      inputs.tld = this.state.tld
    }
    this.props.input_actions.RX__set_inputs(inputs)
  }

  render() {
    let {
      home,
      loading,
      location,
      title,
      title_nav,
      placeholder,
      cue,
      cue_nav,
      hideInput,
      domains,
      className,
      autofocus,
      ...props
    } = this.props

    let toggleBlip = () => {
      this.setState({ blipActive: !this.state.blipActive })
    }
    let set_contactEmail = (event) => {
      this.setState({ contactEmail: event.target.value })
    }
    let save_contactEmail = (e) => {
      e.preventDefault()
      this.props.contact_actions.RX__contact({
        email: this.state.contactEmail
      })
    }

    return (
      <>
        {/*
         * Header
         */}
        <Header location={location} home={!!home} loading={!!loading} />

        {/*
         * Search banner
         */}
        <Styled
          className={"Search " + className}
          onMouseEnter={() => {
            // if (autofocus) this.focusInput()
          }}
        >
          {/*
           * Beta
           */}
          {!!home && !!domains && (
            <div className={"BetaBlipContainer" + (this.state.blipActive ? " open" : " closed")}>
              <div className="content BetaBlipContent">
                <div className={"BetaBlip"} onMouseOver={toggleBlip} onMouseOut={toggleBlip}>
                  <div className="heading">
                    {/*<span className="">🎉</span>&nbsp;&thinsp; */}
                    <span className="x85">🥳</span>&nbsp;&thinsp; Welcome to our<b> beta </b>preview &thinsp;
                    <FA icon={faAngleDown} className="" /> &nbsp;&thinsp;
                    {/*<span className="x85">🥳</span>*/}
                    <span className="">🎉</span>
                  </div>
                  <div className="content">
                    <p>
                      Thanks for your support and feedback! We're working every day to finish and optimize the product.
                      It will be ready for production scale very soon.
                    </p>
                    <p>Please enter your email to be notified of important updates:</p>
                    <form className="p" onSubmit={save_contactEmail}>
                      <input type="text" placeholder="your email" onChange={set_contactEmail} />
                      <button type="submit">save</button>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/*{!!home && (*/}
          {/*  <div className="beta_banner_container">*/}
          {/*    <div className="beta_banner">Beta preview!</div>*/}
          {/*    /!*<BetaPreviewBanner />*!/*/}
          {/*  </div>*/}
          {/*)}*/}
          <div className="content">
            {/*{!!home && (*/}
            {/*  <div className="BetaPreviewContainer">*/}
            {/*    /!*<div className="beta_banner">Beta preview!</div>*!/*/}
            {/*    <BetaPreviewBanner />*/}
            {/*  </div>*/}
            {/*)}*/}
            <div className="home-zoomed">
              {/*
               * Title
               * If mobile, show only last line (last item in array)!
               */}
              {!!title && (
                <h1 className="title">
                  {title}
                  <div className="title_nav">{title_nav}</div>
                </h1>
              )}

              {/*
               * Input Group
               */}
              <div className={"input-group"}>
                {!hideInput ? (
                  <Input
                    ref={this.Input_ref}
                    className="Input"
                    placeholder={placeholder || "..."}
                    value={this.state.str}
                    onChange={(event) => {
                      this.setState({
                        str: event.target.value.toLowerCase()
                      })
                    }}
                    onKeyPress={(event) => {
                      if (event.key === "Enter") {
                        this.captcha_challenge()
                      }
                    }}
                    onBlur={() => {
                      if (this.props.input_str && !this.state.str) {
                        this.setState({
                          str: this.props.input_str.trim()
                        })
                      }
                    }}
                  />
                ) : (
                  <span className="input-padding" />
                )}
                {!!domains && (
                  <InputTld
                    value={this.state.tld || "com"}
                    placeholder=""
                    handleSelect={(value) => {
                      this.setState(
                        {
                          tld: value.toLowerCase()
                        },
                        this.captcha_challenge
                      )
                    }}
                  />
                )}
                <Button
                  className="Button"
                  onClick={() => {
                    this.captcha_challenge()
                  }}
                >
                  <FA icon={faSearch} className="faSearch" style={{ transform: "scale(0.85)" }} />
                </Button>
              </div>

              {/*
               * Cue message
               */}
              {(!!cue || !!cue_nav) && (
                <div className="cue_container">
                  {!!cue && <span className="cue">{cue}</span>}
                  {!!cue_nav && <span className="cue_nav">{cue_nav}</span>}
                </div>
              )}
            </div>
          </div>

          {/*
           * RecaptchaV2
           */}
          {/*{PRODUCTION ? (*/}
          {/*  <>*/}
          {/*    <RecaptchaV2*/}
          {/*      ref={(ref) => (this.recaptcha2 = ref)}*/}
          {/*      sitekey={CAPTCHA2_KEY}*/}
          {/*      onLoaded={() => {}}*/}
          {/*      onResolved={this.verifyReCaptchaV2}*/}
          {/*      onError={(err) => {*/}
          {/*        console.error("Captcha error", err)*/}
          {/*      }}*/}
          {/*      onExpired={(err) => {*/}
          {/*        console.error("Captcha expired", err)*/}
          {/*      }}*/}
          {/*    />*/}
          {/*    <RecaptchaV3*/}
          {/*      ref={(ref) => (this.recaptcha3 = ref)}*/}
          {/*      sitekey={CAPTCHA3_KEY}*/}
          {/*      action="idk"*/}
          {/*      verifyCallback={this.verifyReCaptchaV3}*/}
          {/*    />*/}
          {/*  </>*/}
          {/*) : (*/}
          {/*  <RecaptchaV2 sitekey="x" onError={this.verifyReCaptchaV2} onExpired={this.verifyReCaptchaV2} />*/}
          {/*)}*/}
        </Styled>
      </>
    )
  }
}

/*******************************************************************************************************
 * this.props DOCUMENTATION
 *******************************************************************************************************/

const mapDispatchToProps = (dispatch) => {
  return {
    output_actions: bindActionCreators(output_actions, dispatch),
    input_actions: bindActionCreators(input_actions, dispatch),
    ui_actions: bindActionCreators(ui_actions, dispatch)
  }
}

const mapStateToProps = function (state) {
  return {
    auth_expires: state.input.auth_expires,
    suggestions_options: state.input.suggestions_options, // bool
    show_poss: state.ui.show_poss, // bool
    input_str: state.input.input_str, // string
    input_captcha: state.input.input_captcha, // string
    chunks: state.output.chunks, // array of DB rows, about input_str
    input_tld: state.input.input_tld, // string
    tlds_user: state.output.tlds_user, // dict of tlds
    tlds_checked: state.output.tlds_checked // dict of tlds
  }
}

Search.propTypes = {
  domains: PropTypes.bool, // if true, this component will manage .tld in url (if false, will omit .tld from url)
  location: PropTypes.object
}

export default connect(mapStateToProps, mapDispatchToProps)(Search)
